<template>
    <v-container style="max-width:740px;">
       <v-col>
            <v-row style="align-items:center; justify-content:flex-start">
                <v-btn text link :to="'/jsp/structure'"> &lt; 이전 페이지</v-btn>
                <v-subheader>그럼 본격적인 코딩을 위해서 코드 구조를 설계 작성하여 봅시다.</v-subheader>
            </v-row>
           <h1>공통 함수</h1>
           <br>
           <p>공통 함수에서는 프로젝트에서 아주 빈번하게 사용되는 함수들을 함께 작성해봅시다. 통신부터 다양한 개발에 필요한 유틸들을 작성해놓았으니 복사 붙여넣기 후 사용하시면 됩니다.</p>
            
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">데이터 통신 (AJAX 통신 : 공통) </v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.ajax = function (options, successcb, failcb) {
            $.ajax({
                type: options.type || 'post',
                url: options.url,
                data: options.data,
                beforeSend: function () {
                
                },
                success: function () {
                    var data = arguments[0];

                    successcb(data);
                },
                error: function () {
                    var jsonError = arguments[0].responseJSON;
                    if (jsonError.status && jsonError.code && jsonError.message && jsonError.detailMessage) {
                        var msg = 'TODO 공통오류처리할것!\r\n';
                        msg += jsonError.status + '\r\n';
                        msg += jsonError.code + '\r\n';
                        msg += jsonError.message + '\r\n';
                        msg += jsonError.detailMessage;
                        alert(msg);
                    }
                    if (failcb) {
                        failcb(arguments);
                    }
                },
                complete: function () {
                
                }
            });
        }
                </code>
            </pre>
            <p>위의 코드는 공통적으로 사용하게 될 AJAX통신의 모습입니다. 위의 공통함수에서는 type값이 존재하지 않을시에 default로 POST로 전송하게 됩니다. beforeSend, success, error, complete로 분기되어 있으며 공통으로 필요한 로직들을 작성하시면 화면에서 해당 공통함수를 사용할 수 있습니다. 또한, 해당 <strong>통신을 동기처리 해야될 때에는 async:false</strong>, 조건을 추가하여 주시면 동기처리됩니다.</p>
             <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">데이터 통신 (AJAX 통신 : 화면) </v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.ajax({
            url: 'api/sample/test',
            type: 'POST',
            data: jsonData
        }, function (data) {
            //성공시 수행
        }, function (data){
            //실패시 수행
        })
                </code>
            </pre>
            <p>실제 화면단에서는 위와 같은 형식으로 공통 함수를 사용하면 되고 첫번째 function에서는 성공시 수행될 로직들이 들어가게 되고, 두번째 function에서는 실패시 동작하는 로직이 들어가게 됩니다.</p>
            
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">폼 데이터 처리(폼 데이터 -> 오브젝트형태)</v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.getFormValue = function (formObj) {
            var arr = formObj.serializeArray();
            if (arr && arr.length > 0) {
                var values = {};
                for (var i in arr) {
                    var obj = arr[i];
                    values[obj.name] = obj.value
                }
                return values;
            }
            return null;
        }
                </code>
            </pre>
            <p>해당 코드는 JSON통신을 하기 전 폼 안의 내용들을 Object형식으로 묶을때 사용하는 코드입니다. Form안의 값들을 Element의 이름과 해당 Element의 vlaue 형태 (ex."name" : "류호진")로 치환됩니다. </p>
            
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">폼 데이터 처리(오브젝트 -> 폼 데이터 셋팅))</v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.setFormValue = function (formObj, jsonData) {
            Utils.getFormValue(formObj);
            $.each(jsonData, function (key, value) {
                var ctrl = formObj.find('[name=' + key + ']');
                if (ctrl.is('select')) {
                    $('option', ctrl).each(function () {
                        if (this.value == value)
                            this.selected = true;
                    });
                } else if (ctrl.is('textarea')) {
                    ctrl.val(value);
                } else {
                    switch (ctrl.attr("type")) {
                        case "text":
                        case "hidden":
                            ctrl.val(value);
                            break;
                        case "checkbox":
                            if (value == 'on') {
                                ctrl.prop('checked', true);
                            } else {
                                ctrl.prop('checked', false);
                            }
                            break;
                    }
                }
            });
        }
                </code>
            </pre>
            <p>해당 코드는 AJAX통신 등을 통하여 데이터를 받아와서 화면상에 출력할때 사용하는 공통 코드입니다. 폼 내부의 Element의 이름을 토대로 값들을 매칭시켜 셋팅하여 줍니다.</p>

            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">폼 데이터 처리(화면 : 폼 -> 오브젝트))</v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        var jsonData = Utils.getFormValue($('#form'));
                </code>
            </pre>
            <p>화면단에서 Utils.getFromValue라는 메소드를 통하여 아이디값으로 폼의 아이디를 넘겨주게 되면 해당 아이디의 name을 보고 매칭하여 데이터를 Object형태로 치환하여 줍니다.</p>

            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">폼 데이터 처리(화면 : 오브젝트 -> 폼))</v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.setFormValue($('#form'), data)
                </code>
            </pre>
            <p>화면단에서는 리턴받은 data를 Utils.setFromValue라는 메소드를 통하여 화면에 뿌려주게 됩니다. 첫번째 파라미터에는 폼의 아이디값을 넣어주면 되고, 두번째 파라미터로는 데이터를 입력하시면 됩니다.</p>

            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">폼 데이터 처리(초기화)</v-subheader>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.resetForm = function (formObj) {
            formObj.find('input:text, input:password, input:file, select, textarea').val('');
            formObj.find('input:radio, input:checkbox').removeAttr('checked').removeAttr('selected');
            //초기화 원하는 것들을 find내에 추가하시면 됩니다.
        }
                </code>
            </pre>
            <p>프로젝트를 진행하다보면 입력화면에서 초기화를 실행해야될 때가 많은데 이러한 상황에서 사용하면 되는 공통 초기화 코드입니다.</p>


            <v-row style="align-items:center; justify-content:flex-end">
                <v-subheader>공통함수에 대해 알아봤으니 보안 취약점을 대응해봅시다.</v-subheader>
                <v-btn text link :to="'/jsp/security'">다음 페이지 ></v-btn>
            </v-row>
       </v-col>
    </v-container>
</template>
<script>
export default {
    name:'JSPUtilView'
}
</script>
