<template>
    <v-container style="max-width:740px;">
        <v-col>
            <v-row style="align-items:center; justify-content:flex-start">
                <v-btn text link :to="'/jsp/util'"> &lt; 이전 페이지</v-btn>
                <v-subheader>화면에 대하여 알아봤으니 자주 사용되는 공통함수를 정의해봅시다.</v-subheader>
            </v-row>
           <h1>보안 가이드</h1>
           <br>
            <p>
                금융 SI에서 프로젝트를 진행하게 되었다면, 웹취약점 분석, 정적 분석, 성능 테스트 등 다양한 테스트에 시달리게 된다. 이러한 보안 취약점들이 발생하게 되면 금전적으로 치명적인 문제가 생길 수 있다. 또한 프로젝트 끝자락에 이러한 문제로 인해서 출시가 늦어지게 되면 폭풍야근 하게 될 수 있기 때문에, 이러한 점을 미연에 방지해보도록 합니다.
            </p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">캐시 컨트롤 웹 취약점</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ tag language="java" pageEncoding="UTF-8" %&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core" %&gt;
        &lt;!DOCTYPE html&gt;
        &lt;html lang="ko"&gt;
        &lt;head&gt;
            &lt;meta charset="utf-8"&gt;
            &lt;meta http-equiv="X-UA-Compatible" content="IE=edge"&gt;

            &lt;meta http-equiv="Cache-Control" content="no-store"/&gt;
            &lt;meta http-equiv="Cache-Control" content="no-cache"/&gt;
            &lt;meta http-equiv="Cache-Control" content="private"/&gt;
            &lt;meta http-equiv="Pragma" content="no-cache"/&gt;
            &lt;meta http-equiv="Expires" content="-1"/&gt;
            &lt;meta name="_csrf" th:content="${_csrf.token}"/&gt;
            &lt;meta name="_csrf_header" th:content="${_csrf.headerName}""/&gt;
            &lt;meta name="viewport" content="width=device-width"/&gt;
            &lt;title&gt;BNK 시스템 프론트엔드 가이드라인&lt;/title&gt;
            &lt;link rel="stylesheet" href='&lt;c:url value="/resources/css/guide.css"/&gt;'&gt;
        &lt;/head&gt;
        &lt;body&gt;
            &lt;jsp:include page="/WEB-INF/views/common/header.jsp"/&gt;
            &lt;jsp:doBody/&gt;
            &lt;script src='&lt;c:url value="/resources/js/guide.js"/&gt;'/&gt;
        &lt;/body&gt;
        &lt;/html&gt;
                </code>
            </pre>
            <p>해당 코드는 구조에서 본 layout.tag 파일의 코드이다. 해당 코드를 그대로 사용하게 되면 Cache-control 웹 취약점이 발견되어 해당 사항을 수정하고 다시 취약점 점검을 보내고 돌아오게 되면 최소 하루이상 걸리기 때문에 이런점을 미연에 방지해야 된다. 아래의 코드를 위의 화면 처럼 적용하면 캐시컨트롤 관련 취약점이 발견되지 않을 것이다.</p>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;meta http-equiv="Cache-Control" content="no-store"/&gt;
        &lt;meta http-equiv="Cache-Control" content="no-cache"/&gt;
        &lt;meta http-equiv="Cache-Control" content="private"/&gt;
        &lt;meta http-equiv="Pragma" content="no-cache"/&gt;
        &lt;meta http-equiv="Expires" content="-1"/&gt;
                </code>
            </pre>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">CSRF(크로스 사이트 요청 위조)</v-subheader>
            <p>Spring Boot와 JSP로 프로젝트를 무작정 진행하다보면 99%의 확률로 CSRF취약점이 발견되어 신입개발자를 괴롭히게 된다. 이런 상황에서 빠르게 벗어나는 방법을 적용하여 봅시다.</p>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;meta name="_csrf" th:content="${_csrf.token}"/&gt;
        &lt;meta name="_csrf_header" th:content="${_csrf.headerName}""/&gt;
                </code>
            </pre>
            <p>Spring Boot에서 Spring Security를 사용하게 되면 폼이나 AJAX로 데이터를 Controller로 전송할때 CSRF토큰 값을 같이 넘기면 해당 문제가 발생하지 않습니다.</p>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        Utils.ajaxTest = function (options, successcb, failcb) {
            var header = $("meta[name='_csrf_header']").attr("content")
            var token = $("meta[name='_csrf']").attr("content")
            $.ajax({
                type: options.type || 'post',
                url: options.url,
                data: options.data,
                beforeSend: function (xhr) {
                    xhr.setRequestHeader(header, token)
                },
                success: function () {
                },
                error: function () {
                },
                complete: function () {
                }
            });
        }
                </code>
            </pre>
            <p>위와 같은 방식으로 데이터를 전송하게 전에 HTTP Header에 CSRF를 셋팅하여 주면 이러한 문제점이 발생하지 않게 됩니다.</p>
            <br>
            <p>또한, 화면 리스트에서 상세화면으로 ID값을 넘겨 상세화면에서 해당 ID를 토대로 데이터를 조회하는 로직으로 동작할 때에도 CSRF문제가 생깁니다. 이런 문제를 해결하기 위한 방법으로는 아래와 같은 방법이 있습니다.</p>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
	&lt;form id="sendData" method="POST"&gt;
		&lt;input type="hidden" name="id" id="id"/&gt;
		&lt;input type="hidden" name="_csrf" id="_csrf"/&gt;
	&lt;/form&gt;
                </code>
            </pre>
            <p>위와 같이 전송할 데이터인 ID와 함께 전송할 CSRF 토큰을 담을 FORM을 생성합니다.</p>
            <pre v-highlightjs>
                <code class="javascript codeSnippet">
        var token = $("meta[name='_csrf']").attr("content");
            $("#sendData").attr('action','&lt;c:url value="/조회주소"/&gt;');
            $("#sendData").find('#id').val(args.item.memberId);
            $("#sendData").find('#_csrf').val(token);
            $("#sendData").submit(); 
                </code>
            </pre>
            <p>그리고 테이블의 ROW를 클릭하였을때 해당 ROW의 ID와 매타테그에서 _csrf값을 받아와 FORM에 셋팅해주고 SUBMIT을 통하여 통신하면 됩니다.</p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">비밀번호 자동완성</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
    &lt;form action="scuirty.html" method="get"&gt;      
        Username: &lt;input type="text" name="firstname" /&gt;&lt;br /&gt;      
        Password: &lt;input type="password" name="lastname" autocomplete="off"/&gt;      
    &lt;input type="submit" value="Submit" /&gt;  &lt;form&gt;
                </code>
            </pre>
            <p>금융 프로젝트 뿐만아니라 다양한 프로젝트에서 발생할 수 있는 문제이다. 비밀번호 autocomplete(자동완성) 제발 OFF하는 습관을 들이도록 합시다.</p>
            <v-subheader>추신. Jquery를 사용하는 프로젝트에서 데이터를 읽을 때, ${변수명} 으로 끝내지말고 jstl을 활용하여 &lt;c:out vlaue="${변수명}"/&gt;을 습관화 하도록 하자</v-subheader>
            
            <v-row style="align-items:center; justify-content:flex-end">
                <v-subheader>해당 내용들을 토대로 만들어진 템플릿을 다운받아 봅시다.</v-subheader>
                <v-btn text @click="downloadTemplate()">다운로드 ></v-btn>
            </v-row>
        </v-col>
    </v-container>
</template>
<script>
export default {
    name:'JSPSecurityView',
    methods:{
        downloadTemplate: function(){
            window.open("https://github.com/ryuhojin/jsptemplate", "_blank");  
        }
    }
}
</script>
<style scoped>
    .codeSnippet {
    color: #a9b7c6 !important;
    background: #282b2e !important;
    display: block !important;
    overflow-x: auto !important;
    padding: 0.5em !important;
    }
</style>