<template>
     <v-container style="max-width:740px;">
       <v-col>
            <v-row style="align-items:center; justify-content:flex-start">
                <v-btn text link :to="'/jsp/about'"> &lt; 이전 페이지</v-btn>
                <v-subheader>프로젝트준비를 시작해봅시다</v-subheader>
            </v-row>
           <h1>코드 구조</h1>
            <br>
            <p>
               프로젝트 구조는 크게 tags(구조)와 view(화면코드)로 나누어 개발하도록 합니다. tags는 공통으로 사용되는 스크립트 및 메타태그 등을 정의 해놓을 수 있어 해당 파일 변경으로 특정 상황에 한번의 수정으로 대처할 수 있습니다.
            </p>
            <br>
            <p>
                지금부터 가이드 내용에 따라 layout.tag를 작성하고 프로젝트를 진행해 보겠습니다. 우선 <strong>tags 폴더 하위에 layout.tag</strong>를 작성하여 주십시오.
            </p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">layout.tag</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ tag language="java" pageEncoding="UTF-8" %&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core" %&gt;
        &lt;!DOCTYPE html&gt;
        &lt;html lang="ko"&gt;
        &lt;head&gt;
            &lt;meta charset="utf-8"&gt;
            &lt;meta http-equiv="X-UA-Compatible" content="IE=edge"&gt;

            &lt;meta name="viewport" content="width=device-width"/&gt;
            &lt;title&gt;BNK 시스템 프론트엔드 가이드라인&lt;/title&gt;
            &lt;link rel="stylesheet" href='&lt;c:url value="/resources/css/guide.css"/&gt;'&gt;
        &lt;/head&gt;
        &lt;body&gt;
            &lt;jsp:include page="/WEB-INF/views/common/header.jsp"/&gt;
            &lt;jsp:include page="/WEB-INF/views/common/sidebar.jsp"/&gt;
            &lt;jsp:doBody/&gt;
            &lt;jsp:include page="/WEB-INF/views/common/footer.jsp"/&gt;
            &lt;script src='&lt;c:url value="/resources/js/guide.js"/&gt;'/&gt;
            &lt;jsp:invoke fragment="script"/&gt;
        &lt;/body&gt;
        &lt;/html&gt;
                </code>
            </pre>
            <p>
                자바 스크립트를 &lt;head&gt;에 포함할 경우, 자바스크립트 파일을 전부 다운로드 하고, 파싱, 컴파일 할 때까지 페이지 렌더링이 지연되기 때문에 &lt;body&gt;하단에 포함하도록 합니다.
            </p>
            <br>
            <p>위의 코드에 대해 부분적으로 분류하면 아래와 같습니다.</p>


            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">퍼블리싱 화면관련 태그</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;meta name="viewport" content="width=device-width"/&gt;
                </code>
            </pre>

            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">공통(헤더,사이드바,메뉴 etc..) 추가 태그</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;jsp:include page="/WEB-INF/views/common/header.jsp"/&gt;
        &lt;jsp:include page="/WEB-INF/views/common/sidebar.jsp"/&gt;
        &lt;jsp:include page="/WEB-INF/views/common/footer.jsp"/&gt;
                </code>
            </pre>
            <p>위의 방식은 헤더, 사이드바, 하단바가 모두 함께 갱신되는 것을 유의해야 됩니다. Vue처럼 SPA형식으로 동작하는 것이 아닙니다.</p>

            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">화면들을 보여주게 될 태그</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;jsp:doBody/&gt;
                </code>
            </pre>
            <p>
                위의 <strong>&lt;jsp:dobdy/&gt;</strong> 부분에 .jsp파일(화면파일) 내의 <strong>&lt;t:layout&gt;&lt;/t:layout&gt;</strong>안에 작성된 코드가 화면에 출력됩니다.
            </p>
            <br>
            <p>지금부터 실질적인 개발할 화면의 구조에 대해서 작성해보도록 합니다</p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">sample.jsp(화면) JSP만 존재하는 경우</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ page language="java" contentType="text/html; charset=UTF-8" pageEncoding="UTF-8" %&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core" %&gt;
        &lt;%@ taglib prefix="t" tagdir="/WEB-INF/tags" %&gt;
        
        &lt;t:layout&gt;

            //JSP내용

        &lt;/t:layout&gt;
                </code>
            </pre>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">sample.jsp(화면)화면내 스크립트가 존재하는 경우</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ page language="java" contentType="text/html; charset=UTF-8" pageEncoding="UTF-8" %&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core" %&gt;
        &lt;%@ taglib prefix="t" tagdir="/WEB-INF/tags" %&gt;
        &lt;jsp:attribute name="script"&gt;

            //스크립트 작성
        &lt;/jsp:attribute&gt;
        &lt;t:layout&gt;

            //JSP내용
        &lt;/t:layout&gt;
                </code>
            </pre>
            <p>위의 화면에서 <strong>&lt;t:layout&gt; &lt;/t:layout&gt;</strong> 내부에 작성된 코드가 화면에 출력됩니다. 또한 해당 페이지 내에서 동작할 함수등을 스크립트 태그 내부에 작성하여 주시면 됩니다.</p>
            <p>이렇게 작성된 프로젝트에서 컨트롤러를 통하여 해당 예시(ex. sample.jsp)를 호출하면 layout.tag파일의 구조처럼 위 아래로 헤더와 푸터가 결합되어 화면에 뿌려지게 됩니다. 프로젝트 진행시 위와 같은방식으로 프론트부분을 구조화 한다면 보다 관리가 편한 프로젝트가 될 수 있다고 생각합니다.</p>
            <br>
            <p>이런 구조화를 응용한다면 화면 <strong>DEPTH를 표현할 때</strong> 도 유용하게 사용할 수 있습니다. 아래 화면은 <strong>tags폴더 하위</strong>에 작성된 <strong>content-header.tag</strong> 파일입니다.</p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">content-header.tag</v-subheader>
        <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ tag language="java" pageEncoding="UTF-8"%&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core"%&gt;
        &lt;%@ attribute name="title" type="java.lang.String"%&gt;
        &lt;%@ attribute name="breadcrumb" type="java.lang.String"%&gt;

        &lt;section class="content-header"&gt;
        &lt;h2&gt;
            &lt;strong&gt;&lt;i class="fa fa-play-circle"&gt;&lt;/i&gt;&lt;c:out value="${title}"/&gt;&lt;/strong&gt;
        &lt;/h2&gt;
        &lt;ol class="breadcrumb"&gt;
            &lt;li&gt;&lt;a href="&lt;c:url value="/"/&gt;"&gt;&lt;i class="fa fa-home"&gt;&lt;/i&gt; Home&lt;/a&gt;&lt;/li&gt;

            &lt;%
                String[] items = breadcrumb.split("\\/");
                for (String item : items) {
                    if (title.equals(item)) {
                        out.print("&lt;li class='active'&gt;" + item + "&lt;/li&gt;");
                    }
                    else {
                        out.print("&lt;li&gt;" + item + "&lt;/li&gt;");
                    }
                }
            %&gt;
        &lt;/ol&gt;
        &lt;/section&gt;
                </code>
            </pre>
            <p>위의 코드는 아래코드에서 처럼 사용되어 공통적으로 자주사용하는 DEPTH 표기화면을 쉽게 추가할 수 있습니다. 태그에 attribute를 통하여 데이터를 넘기고 넘겨받은 데이터를 내부 로직을 통해 화면에 적절하게 뿌려준다면 무궁무진한 응용이 가능합니다.</p>
            <v-subheader style="background-color:#f6f6f6; color:#000; font-weight:bold">sample.jsp(화면) 내의 &lt;t:content-header&gt; 사용 예시</v-subheader>
            <pre v-highlightjs>
                <code class="HTML codeSnippet">
        &lt;%@ page language="java" contentType="text/html; charset=UTF-8" pageEncoding="UTF-8" %&gt;
        &lt;%@ taglib prefix="c" uri="http://java.sun.com/jsp/jstl/core" %&gt;
        &lt;%@ taglib prefix="t" tagdir="/WEB-INF/tags" %&gt;
        
        &lt;t:layout&gt;
        &lt;t:content-header title="교육 정보" breadcrumb="교육관리/교육 정보"/&gt;

            &lt;script&gt;

            &lt;/script&gt;
        &lt;/t:layout&gt;

                </code>
            </pre>
            <p>위와 같이 화면 구조에 대한 기초를 잡으셨다면 이제는 기능에 대해서 작성해 봅시다.</p>
            <v-row style="align-items:center; justify-content:flex-end">
                <v-subheader>화면에 대하여 알아봤으니 자주 사용되는 공통함수를 정의해봅시다.</v-subheader>
                <v-btn text link :to="'/jsp/util'">다음 페이지 ></v-btn>
            </v-row>
       </v-col>
    </v-container>
</template>
<script>
export default {
    name:'JSPStructureView'
}
</script>
<style scoped>
    .codeSnippet {
    color: #a9b7c6 !important;
    background: #282b2e !important;
    display: block !important;
    overflow-x: auto !important;
    padding: 0.5em !important;
    }
</style>